.Icon{
    cursor:pointer;
    display:flex;
    justify-content:center;
    align-items:center;
    height:48px;
    width:48px;
    font-size:24px;
    font-weight:bold;
  
  }
  
  
  .Icon-container-check{
    position: absolute;
    left: 12px;
  }
  .Icon-container-check--active{
  color:#116414;
  }
  .Icon-container-check{
    position:absolute;
    top:-24px;
    right:0;
  }
  .Icon-container-delete:hover{
    color:rgb(103, 4, 26);
  }
  
  .Icon-svg{
  width:24px;
  height:24px;
  }

  .Icon-container-checked:hover .Icon-svg{
    fill:green;
  }
  .Icon-container-delete:hover .Icon-svg{
    fill: red;
  }