.TodoItem{
  background-color:#FAFAFA ;
  position:relative;
  display:flex;
  justify-content:center;
  align-items:center;
  margin-top:24px;
  box-shadow:0px 5px 50px rgba(23, 26, 30, 0.15);
}
.TodoItem-p{
  margin:24px 0 24px 24px;
  width:calc(100% - 120px);
  font-size:18px;
  line-height:24px;
  font-weight:400;
}
.TodoItem-p--complete{
  text-decoration:line-through;
}

