.TodoButton{
    background-color: cornflowerblue;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 05);
    border:none;
    border-radius:50%;
    cursor:pointer;
    font-size:50px;
    position:fixed;
    bottom:24px;
    right: 24px;
    font-weight:bold;
    color: #FAFAFA;
    display: flex;
    justify-content:center;
    height: 64px;
    width: 64px;

    transform: rotate(0);
    transition: .3s ease;
}

.TodoButton:hover{
    transform:rotate(224deg);
}
